<template>
    <div class="video">
        <div class="videoTop">
            <van-search
                v-model="searchvalue"
                show-action
                shape="round"
                placeholder="请输入搜索关键词"
                input-align="center"
                left-icon
            >
                <template #action>
                    <div class="personal" v-if="personalimg">
                        <img :src="personalimg" alt />
                    </div>
                    <div class="nologin" v-else @click="goLogin">
                        <img src="../../assets/img/nologin.svg" alt />
                    </div>
                </template>
                <template #left>
                    <div class="icon"></div>
                </template>
            </van-search>
        </div>
        <div class="videoBody">
            <van-tabs v-model="active" @click="clickTabs">
                <van-tab
                    :title="item.title"
                    v-for="(item, index) in tabData"
                    :key="index"
                >
                    <div class="videoList" :data-index="index">
                        <div
                            class="videoListItem"
                            v-for="(v, i) in videoListData"
                            :key="i"
                        >
                            <div class="videoBox">
                                <img v-show="v.isShow" :src="v.cover" alt />
                                <div class="videoPosition"></div>

                                <div class="showbox">
                                    <img
                                        :src="
                                            v.isShow
                                                ? videobtnUrl1
                                                : videobtnUrl2
                                        "
                                        @click="clickshowbox(index, i)"
                                    />
                                </div>
                            </div>
                            <div class="titleAndImgBox flexbetween">
                                <div class="title">
                                    <span class="one-ellipsis">{{
                                        v.name
                                    }}</span>
                                </div>
                                <div class="imgBox">
                                    <img v-if="v.picUrl" :src="v.picUrl" alt />
                                    <p v-else>无</p>
                                </div>
                            </div>
                            <div class="commentBox flexbetween">
                                <div class="commentBoxLeft">
                                    <div class="good">
                                        <img
                                            src="../../assets/img/good.svg"
                                            alt
                                        />
                                        <p class="num">{{ v.likedCount }}</p>
                                    </div>
                                    <div class="talk">
                                        <img
                                            src="../../assets/img/talk.svg"
                                            alt
                                        />
                                        <p class="num">{{ v.commentCount }}</p>
                                    </div>
                                </div>
                                <div class="commentBoxRight">
                                    <van-icon name="ellipsis" size="20" />
                                </div>
                            </div>
                        </div>
                    </div>
                </van-tab>
            </van-tabs>
        </div>
        <div class="videoFoot"></div>
    </div>
</template>

<script>
// import VideoBox from "../../components/Videobox";
import "../../assets/less/videopage.less";
import { createNamespacedHelpers } from "vuex";
import { setupCookie } from "../../assets/js/setupCookie";
const {
    mapState: musicState,
    mapActions: musicActions
} = createNamespacedHelpers("MusicAudioStore");
export default {
    // components: {
    //   VideoBox,
    // },
    data() {
        return {
            showbox: "block",
            active: 0,
            // 搜索框
            searchvalue: "",
            //头像
            personalimg: "",
            //视频数据
            videoListData: [],
            //tab数据
            tabData: [
                { title: "内地" },
                { title: "港台" },
                { title: "欧美" },
                { title: "日本" },
                { title: "韩国" }
            ],
            videobtnUrl1: require("../../assets/img/videoplay.svg"),
            videobtnUrl2: require("../../assets/img/videosuspend.svg")
        };
    },
    computed: {
        ...musicState(["iconNum"])
    },
    created() {
        //获取用户登录状态
        this.getuserStatus();
    },
    mounted() {
        //获取全部mv
        this.getAllVideo(this.tabData[0].title);
    },
    methods: {
        ...musicActions(["seticonNum"]),
        //跳转到登录
        goLogin() {
            this.$router.push({ name: "Login" });
        },

        //获取用户登录状态
        getuserStatus() {
            //获取cookie
            let cookie = setupCookie.getCookies();
            if (cookie.split(";")[0] == "null") {
                this.personalimg = null;
                return;
            }
            this.axios({
                method: "GET",
                url: "/login/status",
                params: {
                    cookie
                }
            })
                .then(result => {
                    this.personalimg = result.data.profile.avatarUrl;
                })
                .catch(err => {});
        },

        //点击切换获取当前地区的mv
        clickTabs(name, title) {
            //获取全部mv
            this.getAllVideo(title);

            // 获取全部
            let videoPositions = document.getElementsByClassName(
                "videoPosition"
            );

            //全部删掉video
            videoPositions.forEach(element => {
                element.innerHTML = "";
            });

            //设置全部图片显示
            for (let i = 0; i < this.videoListData.length; i++) {
                this.videoListData[i].isShow = true;
            }
        },

        //获取全部mv
        getAllVideo(area) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "GET",
                url: "/mv/all",
                params: {
                    limit: 5,
                    area
                }
            })
                .then(result => {
                    this.$toast.clear();
                    this.videoListData = [];
                    if (result.status == 200) {
                        let data = [];
                        for (let key in result.data.data) {
                            if (!data[key]) {
                                data[key] = {
                                    name: result.data.data[key].name,
                                    mvId: result.data.data[key].id,
                                    artistId: result.data.data[key].artistId,
                                    cover: result.data.data[key].cover,
                                    isShow: true
                                };
                            }
                        }
                        for (let i = 0; i < data.length; i++) {
                            this.getVideoInfo(data[i], data[i].mvId);
                            this.getSongInfo(data[i], data[i].artistId);
                            this.getVideoUrl(data[i], data[i].mvId);
                        }
                        this.videoListData = data;
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取最新MV 无引用
        getVideoList() {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "GET",
                url: "/mv/first",
                params: {
                    limit: 5
                }
            })
                .then(result => {
                    this.$toast.clear();
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取mv数据 无引用
        getvideoData(mvid) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "GET",
                url: "/mv/detail",
                params: {
                    mvid
                }
            })
                .then(result => {
                    this.$toast.clear();
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取mv点赞转发评论数数据
        getVideoInfo(v, mvid) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "GET",
                url: "/mv/detail/info",
                params: {
                    mvid
                }
            })
                .then(result => {
                    this.$toast.clear();
                    if (result.status == "200") {
                        v.commentCount = result.data.commentCount;
                        v.likedCount = result.data.likedCount;
                        // vue手动刷新页面
                        this.$forceUpdate();
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取歌手描述（图片）
        getSongInfo(v, id) {
            if (id == 0) {
                return;
            }
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "GET",
                url: "/artist/album",
                params: {
                    id
                }
            })
                .then(result => {
                    this.$toast.clear();
                    if (result.status == 200) {
                        v.picUrl = result.data.artist.picUrl;
                        // vue手动刷新页面
                        this.$forceUpdate();
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取mv播放地址
        getVideoUrl(v, id) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "GET",
                url: "/mv/url",
                params: {
                    id,
                    r: "720"
                }
            })
                .then(result => {
                    this.$toast.clear();
                    if (result.status == 200) {
                        v.videoUrl = result.data.data.url;
                        // vue手动刷新页面
                        this.$forceUpdate();
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //点击隐藏播放视频
        clickshowbox(index, inum) {
            if (this.iconNum == 1) {
                this.seticonNum(0);
            }
            let videoLists = document.getElementsByClassName("videoList");
            // 获取全部
            let videoPositions = document.getElementsByClassName(
                "videoPosition"
            );
            //获取当前的
            let newShowVideoLists = "";
            for (let i = 0; i < videoLists.length; i++) {
                if (videoLists[i].getAttribute("data-index") == index) {
                    newShowVideoLists = videoLists[i].getElementsByClassName(
                        "videoPosition"
                    );
                }
            }
            //全部删掉video
            videoPositions.forEach(element => {
                element.innerHTML = "";
            });
            //设置video url
            let url = this.videoListData[inum].videoUrl;
            newShowVideoLists[inum].innerHTML = `<video src=${url}></video>`;
            //设置视频播放
            if (this.videoListData[inum].isShow) {
                document.querySelector("video").play();
            } else {
                document.querySelector("video").pause();
            }
            for (let i = 0; i < this.videoListData.length; i++) {
                if (i == inum) {
                    continue;
                }
                if (!this.videoListData[i].isShow) {
                    this.videoListData[i].isShow = true;
                    break;
                }
            }
            //设置显示
            this.videoListData[inum].isShow = !this.videoListData[inum].isShow;
        }

        //
    }
};
</script>

<style lang="less" scoped>
</style>